.popup__modal {
  min-width: 430px;
  min-height: 330px;
  background-color: white;
  border-radius: 10px;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  position: relative;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 756px) {
  .popup__modal {
    min-width: 282px;
    min-height: 322px;
    padding: 25px 22px;
  }
}
